import React from "react";

function HowItWorksPage() {
  return (
    <div style={{ padding: "30px 20px", textAlign: "left", lineHeight: "1.8", fontSize: "18px" }}>
      <h2 style={{ textAlign: "center", marginBottom: "20px", fontSize: "28px" }}>Ako to funguje</h2>
      <p>
        <strong>Popis aplikcie Vocabular</strong><br />
        Vocabular je desktopov aplikcia vyvinut pomocou frameworku Electron. Aplikcia sli na precviovanie slovok v anglitine a slovenine. Pouvate si me pridva vlastn slovka, testova sa z existujcej databzy a spravova zoznam slovok. Aplikcia je navrhnut tak, aby bola jednoducho pouiten a prispsoben potrebm pouvatea.
      </p>
      <p>
        <strong>Funkcionality programu</strong>
      </p>
      <ul style={{ paddingLeft: "20px" }}>
        <li><strong>Zobrazenie slovok:</strong> Pouvate si me pozrie kompletn zoznam slovok, ktor s aktulne uloen v databze (<code>words.json</code>).</li>
        <li><strong>Pridvanie slovok:</strong> Pouvate me prida nov slovka do databzy pomocou textovch pol na zadanie anglickho a slovenskho prekladu.</li>
        <li><strong>Mazanie slovok:</strong> Kad slovko v zozname je mon vymaza pomocou dedikovanho tlaidla.</li>
        <li><strong>Vyhadvanie slovok:</strong> Pouvate me filtrova zoznam slovok poda zadanho textu (anglickho alebo slovenskho).</li>
        <li><strong>Testovanie slovok:</strong> Aplikcia umouje spustenie testu, kde pouvate preklad nhodne vybran slovka. Po ukonen testu sa zobraz tatistika.</li>
        <li><strong>Prehrvanie vslovnosti:</strong> Pri kadom slovku je k dispozcii tlaidlo na prehratie vslovnosti anglickho slovka.</li>
      </ul>
      <p>
        <strong>Podpora vekho mnostva slovok:</strong><br />
        Program Vocabular je navrhnut tak, aby podporoval vek poet slovok, a to bez vraznho obmedzenia. Ukladanm dajov do sboru <code>words.json</code> sa zabezpeuje, e mete prida stovky a tisce slovok bez straty vkonu. Program zrove obsahuje funkcie na rchle vyhadvanie, sprvu a precviovanie slovok, m je idelny pre kadho pouvatea.
      </p>
    </div>
  );
}

export default HowItWorksPage;
