import React from "react";
import { Container, Box, Typography } from "@mui/material";

function HomePage() {
  const features = [
    { title: "Zobrazenie slovok", description: "Pouvate si me pozrie kompletn zoznam slovok, ktor s aktulne uloen v databze." },
    { title: "Pridvanie slovok", description: "Pouvate me prida nov slovka do databzy pomocou textovch pol na zadanie Anglickho a Slovenskho prekladu." },
    { title: "Mazanie slovok", description: "Kad slovko v zozname je mon vymaza pomocou dedikovanho tlaidla." },
    { title: "Vyhadvanie slovok", description: "Pouvate me filtrova zoznam slovok poda zadanho textu (Anglickho alebo Slovenskho)." },
    { title: "Testovanie slovok", description: "Aplikcia umouje spustenie testu, kde pouvate preklad nhodne vybran slovka. Po ukonen testu sa zobraz tatistika." },
    { title: "Prehrvanie vslovnosti", description: "Pri kadom slovku je k dispozcii tlaidlo na prehratie vslovnosti Anglickho slovka." },
  ];

  return (
    <Container sx={{ textAlign: "center", my: 5 }}>
      <Typography variant="h3" gutterBottom>
        Vitajte na strnke Vocabular!
      </Typography>
      <Typography variant="h5" gutterBottom>
        Objavte vetky monosti aplikcie na uenie Anglicko-Slovenskch slovok.
      </Typography>
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(300px, 1fr))",
          gap: 3,
          my: 5,
        }}
      >
        {features.map((feature, index) => (
          <Box
            key={index}
            sx={{
              border: "1px solid #ddd",
              borderRadius: "8px",
              padding: 2,
              transition: "transform 0.3s, box-shadow 0.3s",
              "&:hover": {
                transform: "scale(1.05)",
                boxShadow: "0px 4px 20px rgba(0,0,0,0.3)",
              },
            }}
          >
            <Typography variant="h6" gutterBottom>
              {feature.title}
            </Typography>
            <Typography variant="body1">{feature.description}</Typography>
          </Box>
        ))}
      </Box>
    </Container>
  );
}

export default HomePage;
